import Zmage from "react-zmage";
import React, { useState } from "react";
import { OverlayTrigger, Popover } from "react-bootstrap";

export default function Portfolio({ data }) {
  if (data == null) return null;

  const projects = data.projects.map((project, index) => (
    <Project project={project} key={index} />
  ));

  return (
    <section id="portfolio">
      <div className="row">
        <div className="twelve columns collapsed">
          <h1>Check Out Some of My Projects.</h1>

          <div
            id="portfolio-wrapper"
            className="bgrid-quarters s-bgrid-thirds cf"
          >
            {projects}
          </div>
        </div>
      </div>
    </section>
  );
}

function Project({ project, key }) {
  const [showPopover, setShowPopover] = useState();

  const popover = (title, category) => (
    <Popover
      id="popover-basic"
      style={{
        width: 330,
        padding: 5,
        border: "2px solid #888888",
        borderRadius: 5,
        backgroundColor: "#eeeeee",
      }}
    >
      <Popover.Title
        style={{
          textAlign: "center",
          backgroundColor: "#cccccc",
          borderBottom: "1px solid #aaaaaa",
        }}
        as="h3"
      >
        {title}
      </Popover.Title>
      <Popover.Content style={{ color: "#444444" }}>{category}</Popover.Content>
    </Popover>
  );

  const projectImage = "images/portfolio/" + project.image;

  return (
    <OverlayTrigger
      show={showPopover}
      trigger="hover"
      placement="auto"
      overlay={popover(project.title, project.category)}
    >
      <div
        key={key}
        className="columns portfolio-item"
        onClick={() => {
          setShowPopover(false);
          setTimeout(() => setShowPopover(), 100);
        }}
      >
        <div className="item-wrap">
          <Zmage alt={project.title} src={projectImage} />
          <a style={{ textAlign: "center" }} href={project.url} target="blank">
            {project.title}
          </a>
        </div>
      </div>
    </OverlayTrigger>
  );
}
